import stalinSort from '../algorithms/StalinSort';

import { Select, MenuItem } from '@mui/material';
import sleepSort from '../algorithms/SleepSort';
import deleteSort from '../algorithms/DeleteSort';
import treeSort from '../algorithms/TreeSort';

const algorithms = [
  {
    name: 'Bubble Sort',
    function: bubbleSort,
  },
  {
    name: 'Quick Sort',
    function: quickSort,
  },
  {
    name: 'Merge Sort',
    function: mergeSort,
  },
  {
    name: 'Insertion Sort',
    function: insertionSort,
  },
  {
    name: 'Selection Sort',
    function: selectionSort,
  },
  {
    name: 'Cocktail Shaker Sort',
    function: cocktailShakerSort,
  },
  {
    name: 'Radix Sort',
    function: radixSort,
  },
  {
    name: 'Intro Sort',
    function: introSort,
  },
  {
    name: 'Heap Sort',
    function: heapSort,
  },
  {
    name: 'Block Sort',
    function: blockSort,
  },
  {
    name: 'Shell Sort',
    function: shellSort,
  },
  {
    name: 'Tim Sort',
    function: timSort,
  },
  {
    name: 'Exchange Sort',
    function: exchangeSort,
  },
  {
    name: 'Cycle Sort',
    function: cycleSort,
  },
  {
    name: 'Patience Sort',
    function: patienceSort,
  },
  {
    name: 'Smooth Sort',
    function: smoothSort,
  },
  {
    name: 'Strand Sort',
    function: strandSort,
  },
  {
    name: 'Comb Sort',
    function: combSort,
  },
  {
    name: 'Gnome Sort',
    function: gnomeSort,
  },
  {
    name: 'Odd-Even Sort',
    function: oddEvenSort,
  },
  {
    name: 'Pigeonhole Sort',
    function: pigeonholeSort,
  },
  {
    name: 'Bucket Sort',
    function: bucketSort,
  },
  {
    name: 'Counting Sort',
    function: countingSort,
  },
  {
    name: 'Spread Sort',
    function: spreadSort,
  },
  {
    name: 'Pancake Sort',
    function: pancakeSort,
  },
  {
    name: 'I Can\'t Believe It Can Sort',
    function: iCantBelieveItCanSort,
  },
  {
    name: 'Bitonic Sort',
    function: bitonicSort,
  },
  {
    name: 'Bogo Sort',
    function: bogoSort
  },
  {
    name: 'Stooge Sort',
    function: stoogeSort,
  },
  {
    name: 'Slow Sort',
    function: slowSort,
  },
  {
    name: 'Stalin Sort',
    function: stalinSort,
  },
  {
    name: 'Sleep Sort',
    function: sleepSort,
  },
  {
    name: 'Delete Sort',
    function: deleteSort,
  },
  {
    name: 'Tree Sort',
    function: treeSort,
  }
];

const AlgorithmSelector = (props) => {
  const setAlgorithm = props.setAlgorithm;
  const algorithm = props.algorithm;
  const onChange = props.onChange;

  return (
    <Select
      onChange={(e) => {onChange(e); setAlgorithm(algorithms.find(algo => algo.name === e.target.value))}}
      value={algorithm.name}
    >
      {algorithms.map((algo, index) => (
        <MenuItem key={index} value={algo.name}>{algo.name}</MenuItem>
      ))}
    </Select>
  );
};

export default AlgorithmSelector;